import {SET_FILTER} from './actionTypes.js';
import {ChessTypes} from '../constants.js'

export default (state = ChessTypes.NONE, action) => {
  switch(action.type) {
    case SET_FILTER: {
      return action.filter;
    }
    default:
      return state;
  }
}



